<template>
    <v-container style="width: 100%; " align="center">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <!--<v-col></v-col>-->
                <v-col class="text-center">
                    <v-toolbar-title>{{rankingText}}</v-toolbar-title>
                </v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>
        </v-app-bar>

        <v-row v-if="users.length > 0" class="text-center justify-center" dense
               style="margin-top: 64px; padding-bottom: 80px">
            <div
                    style="width: 100%"
                    v-for="(user,index) in users"
                    :key="user.id"
            >
                <v-card
                        v-if="user.id != myUserID"
                        style="padding: 5px"
                        @click="$router.push({path: '/dashboard/search/user/' + user.id})">
                    <v-row>
                        <v-col cols="3">
                            <h2>{{index+1}}</h2>
                        </v-col>
                        <v-col>
                            <v-avatar style="width: 20%; padding-top: 20%; vertical-align: top; border-radius: 50%">
                                <img
                                        style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                                        class="white--text align-end"
                                        :aspect-ratio="1"
                                        :src="user.profileImg"
                                >
                            </v-avatar>
                        </v-col>
                        <v-col>
                            <div>
                                <strong style="font-size: 12px">@{{user.username}}</strong>
                                <p>
                                    <v-icon color="yellow darken-3">mdi-star</v-icon>
                                    {{user.score}}
                                </p>
                            </div>
                        </v-col>
                    </v-row>
                </v-card>
                <v-card
                        v-if="user.id == myUserID"
                        style="padding: 5px"
                        dark
                        color="#1976D2">
                    <v-row>
                        <v-col cols="3">
                            <h2>{{index+1}}</h2>
                        </v-col>
                        <v-col>
                            <v-avatar style="width: 20%; padding-top: 20%; vertical-align: top; border-radius: 50%">
                                <img
                                        style="width: 100%; position: absolute; top: 0; height: 100%; object-position: center; object-fit: cover;"
                                        class="white--text align-end"
                                        :aspect-ratio="1"
                                        :src="user.profileImg"
                                >
                            </v-avatar>
                        </v-col>
                        <v-col>
                            <div>
                                <strong style="font-size: 12px">@{{user.username}}</strong>
                                <p>
                                    <v-icon color="yellow darken-3">mdi-star</v-icon>
                                    {{user.score}}
                                </p>
                            </div>
                        </v-col>
                    </v-row>
                </v-card>
                <br>
            </div>
            <v-progress-circular
                    v-if="pageLoading"
                    indeterminate
                    color="primary"
                    style="margin-bottom: 50px"
            ></v-progress-circular>
        </v-row>

        <v-btn
                v-scroll="onScroll"
                style="z-index: 3; bottom: 80px"
                v-show="fabScroll && users.length > 0"
                fab
                dark
                fixed
                right
                color="primary"
                @click="toTop"
        >
            <v-icon>mdi-arrow-up</v-icon>
        </v-btn>
        <v-row v-if="users.length == 0">
            <p>{{problemRetrievingRankingText}}</p>
        </v-row>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
        <!--<v-row>
            <v-col>

                <v-container>
                    <v-btn color="primary" @click="listRankingUsers" v-if="!buttonIsDisabled"
                           :disabled="buttonIsDisabled"
                           class="bottom-centered-buttons">
                        {{watchMoreButtonText}}
                    </v-btn>
                </v-container>
            </v-col>
        </v-row>-->
    </v-container>
</template>

<script>
    import {mapActions, mapGetters, mapMutations, mapState} from 'vuex';

    export default {
        name: "RankingHome",
        data() {
            return {
                problemRetrievingRankingText: null,
                rankingText: null,
                snackbarText: null,
                snackbar: false,
                closeText: null,
                buttonIsDisabled: false,
                myUserID: this.$store.getters['user/getID'],
                pageLoading: false,
                fabScroll: false
            }
        },
        methods: {
            ...mapActions('ranking', ['getPagedRankingUsers', 'areThereMoreRankingUsersToShow']),
            onScroll(e) {
                if (typeof window === 'undefined') return
                const top = window.pageYOffset || e.target.scrollTop || 0
                this.fabScroll = top > 20
            },
            toTop() {
                this.$vuetify.goTo(0)
            },
            listRankingUsers: function () {
                let self = this;
                self.buttonIsDisabled = true;
                let resultGetPagedRankingUsers = self.getPagedRankingUsers();

                return resultGetPagedRankingUsers.then((moreUsersToShow) => {
                    self.buttonIsDisabled = !moreUsersToShow;
                    self.pageLoading = false;
                })
                    .catch((error) => {
                        self.pageLoading = false;
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
        },
        created: async function () {
            this.problemRetrievingRankingText = this.$i18n.t('messages.ERROR_PROBLEM_RETRIEVING_RANKING');
            this.rankingText = this.$i18n.t('messages.TEXT_RANKING');


            let self = this;
            let $ = require('jquery');

            if (self.users.length == 0) {
                let available = true;
                self.buttonIsDisabled = true;

                let first = true;

                while (first || (available && $(document).height() <= $(window).height())) {
                    first = false;
                    await self.getPagedRankingUsers().then((moreUsersToShow) => {
                        available = moreUsersToShow;
                        self.pageLoading = false;
                    })
                        .catch((error) => {
                            self.snackbarText = error;
                            self.snackbar = true;
                            self.pageLoading = false;
                        });
                }
                self.buttonIsDisabled = !available;

            } else {
                self.areThereMoreRankingUsersToShow().then((moreUsersToShow) => {
                    self.buttonIsDisabled = !moreUsersToShow;
                });
            }
        },
        mounted() {
            let self = this;
            let $ = require('jquery');

            $(window).scroll(function () {
                let bottom_position = $(document).height() - ($(window).scrollTop() + $(window).height());

                if (!self.buttonIsDisabled && bottom_position < 1) {
                    self.pageLoading = true;
                    self.listRankingUsers();
                }
            });
        },
        computed: {
            ...mapGetters('ranking', {users: 'getRankingUsersToShow'}),
        }
    }
</script>

<style scoped>
    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }
</style>